import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
const routes: Routes = [
  {
    path: 'products',
    loadChildren: () =>
      import('./product/list/list-bot.module').then((m) => m.ListProductModule),
  },
  {
    path: 'product/new',
    loadChildren: () =>
      import('./product/form/form-bot.module').then((m) => m.FormProductModule),
  },
  {
    path: 'product/:id',
    loadChildren: () =>
      import('./product/form/form-bot.module').then((m) => m.FormProductModule),
  },

  {
    path: 'orders',
    loadChildren: () =>
      import('./order/list/list-order.module').then((m) => m.ListProductModule),
  },
  {
    path: 'order/new',
    loadChildren: () =>
      import('./order/form/form-order.module').then((m) => m.FormOrderModule),
  },
  {
    path: '404',
    loadChildren: () =>
      import('../../modules/errors/error-404/error-404-routing.module').then(
        (m) => m.Error404RoutingModule
      ),
  },
  { path: '**', redirectTo: '/bots' },
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class VoicebotRoutingModule {}
